.p-snavi {
	width: 90%;
	background: $color--main;
	top: 20px;
	right: 0;
	position: absolute;
	z-index: 3;
	&__menu {
		z-index: 4;
		&__item {
			width: 100%;
			height: 50px;
			line-height: 50px;
			text-align: left;
			color: #fff;
			border-bottom: 1px solid $color--main--dark;
			box-sizing: border-box;
			&.is-active {
				height: 100%;
				line-height: auto;
			}
			a {
				width: 100%;
				height: 100%;
				padding: 0 20px;
				color: #fff;
				text-decoration: none;
				display: block;
				box-sizing: border-box;
			}
			i {
				margin-right: 10px;
				&::after {
				}
			}
			span {
				margin-left: 12px;
			}
			&--close {
				@extend .p-snavi__menu__item;
				text-align: right;
				a {
					width: 16px;
					height: 16px;
					margin-top: 16px;
					display: inline-block;
				}
			}
			&--search {
				@extend .p-snavi__menu__item;
				height: 70px;
				padding: 0 15px;
				text-align: left;
				&__ttl {
					height: 20px;
					margin: -5px 0 5px 0;
					padding: 0;
				}
				&__box {
					input {
						width: 100%;
						padding: 0;
						border-radius: 35px;
						background: #fff;
					}
				}
			}
			&--regist {
				@extend .p-snavi__menu__item--search;
				padding: 0;
				height: 50px;
			}
			&--shop {
				@extend .p-snavi__menu__item--search;
				padding: 0;
				height: 50px;
			}
			&__bt {
				position: relative;
				.u-arrow--bottom,
				.u-arrow--top {
					top: 15px;
					right: 20px;
					position: absolute;
				}
				.u-arrow--top {
					top: 25px;
				}
			}
			&--sns {
				// height: 200px;
				padding: 20px;
				&__ttl {
					margin: 0;
					padding: 0 20px 20px 0;
					line-height: auto;
					font-family: 'Raleway', sans-serif;
					font-size: 2.4rem;
					color: #fff;
					text-align: left;
				}
			}
		}//&__item
		&__lv2 {
			height: 100%;
			display: block;
			&__list {
				height: 100%;
				background: $color--main--dark;
				box-sizing: border-box;
				display: flex;
				flex-wrap: wrap;
				.p-snavi__menu__item {
					width: 100%;
					height: 50px;
					border-bottom: 2px solid $color--main;
					box-sizing: border-box;
					&:last-child {
						border-bottom: none;
					}
					a {
						padding: 0 20px;
					}
				}
				.p-snavi__menu__item--w50 {
					@extend .p-snavi__menu__item;
					width: 50%;
					box-sizing: border-box;
					&:nth-child( odd ) {
						border-right: 2px solid $color--main;
					}
				}
			}
			&.is-close {
				display: none;
			}
		}//&__lv2
	}//%menu
}
